import shoppingcatlist from "@/components/shoppingcartlist/shoppingcartlist.vue"
import shopingbottompay from "@/components/shoppingcartlist/shopingbottompay.vue"
const app = getApp();
export default {
	
	data() {
		return {
			totalpic:0,
			bool:true,
			zizujianlist:[],
			xiabiao:null,
			returnsindex:[],
			tokey:"",
			carid:"",
			shopinglist:[],
			delatestaticbool:false
		}
	},
	methods: {
		//这是传过来的价格
		price(e){
			if(e){//判断传过来的价格有没有 有的话 就采用 没有的话就赋值0
				this.totalpic = e
			}else{
				this.totalpic = 0
			}
			
		},
		//这是传过来的数组
		datalist(e){
			this.zizujianlist = e
		},
		//这是传过来的下标
		dataindex(e){
			this.xiabiao = e
		},
		datacarid(e){
			this.carid = e
		},
		//这是shopingbottompay组件传过来的删除后的数组  在将下标返回去删除
			// console.log(this.totalpic)
		//这是管理
		showmanagement(){
			if(this.bool){
				this.bool = false
			}else{
				this.bool = true
			}
		},
		//封装个请求列表的函数
		UpdateShoppingCartlist(){
			const _this = this
			uni.getStorage({
				key:"bindtokey",
				success(res){
					_this.tokey = res.data
					console.log(_this.tokey)
					uni.request({
						url:"http://hbk.huiboke.com/api/shopping_cart/getShoppingCartList",
						method:"POST",
						data:{
							token:_this.tokey,
							page:1,
							pageSize:10
						},
						success(res){
							if(res.data.code==0){//代表获取成功
									_this.shopinglist = res.data.data
									uni.stopPullDownRefresh();//关闭下拉刷新
							}else{
								console.log("重新登录")
							}
						}
					})
				}
			})
			
		},
		//这是删除购物车的时候传过来的数据
		deleteData(e){
			this.shopinglist = e
		},
		//当用户点击了删除子组件传一个状态过来，父组件用来接收
		deletestatic(e){
			this.delatestaticbool = e
		}
	},
	onLoad(){
		this.statusBar = app.globalData.statusBar
	},
	onShow(){
		//在购物车每次显示的时候 获取用户的tokey值
		const _this = this
		_this.UpdateShoppingCartlist()
	},
	created(){
		const _this = this
		
	},
	components:{
		shoppingcatlist,
		shopingbottompay
	}
}
